<template>
  <div class="search full-screen">
    <div class="page-panel" :class="{'page-panel-yk': (auth && ['cy', 'tz'].indexOf(auth.type) === -1)}">
      <pem-scroll-view 
        ref="scroll"
        class="scroll-view"
        :data="listenList"
        :pullup=true
        :pulldown=true
        @pullUpLoad="listMore"
        @pullDownRefresh="listRefresh">
        <div>
          <div class="scroll-view-pulldown">下拉刷新...</div>
          <!-- 准确匹配 -->
          <div v-if="exactFlag" class="exact-matching" :class="{'borderbottom': (tabtype === 'project' && projectList.length > 0) || (tabtype === 'investor' && investorList.length > 0)}">
            <pem-project-list v-show="tabtype === 'project' && auth && auth.type !== 'cy'" :data="exactList" :isCy="isCy" @alert="alert"/>
            <pem-investor-list v-show="tabtype === 'investor' && auth && auth.type !== 'tz'" :data="exactList" :isCy="isCy" @alert="alert"/>
          </div>
          <!-- 推荐或者未匹配提示 -->
          <div v-if="(tabtype === 'project' && projectList.length > 0) || (tabtype === 'investor' && investorList.length > 0)" class="no-matching">
            <pem-icon name="searchpage-recommend" width="54" height="50"/>
            <span>{{exactFlag ? '' : '暂未找到相关内容，'}}以下是为您推荐内容</span>
          </div>
          <!-- 项目 -->
          <pem-project-list v-show="tabtype === 'project' && auth && auth.type !== 'cy'" :data="projectList" :isCy="isCy" @alert="alert"/>
          <!-- 投资人 -->
          <pem-investor-list v-show="tabtype === 'investor' && auth && auth.type !== 'tz'" :data="investorList" :isCy="isCy" @alert="alert"/>
          <!-- 项目或者投资人为空 -->
          <div v-if="!isLoading && !exactFlag && ((tabtype === 'project' && projectList.length === 0) || (tabtype === 'investor' && investorList.length === 0))" class="empty-result">
            <div class="empty-result-icon">
              <pem-icon name="searchpage-empty" width="356" height="356"/>
            </div>
            <div>暂未找到相关{{ tabtype === 'project' ? '项目' : '投资人'}}</div>
          </div>
          <div class="scroll-view-pullup" v-if="(tabtype === 'project' && projectList.length > 0) || (tabtype === 'investor' && investorList.length > 0)">{{ isMore ? '加载中...' : '目前就这么多了' }}</div>
        </div>
      </pem-scroll-view>
    </div>
    <!-- 顶部固定搜索 -->
    <div class="search-list-header">
      <pem-search :svalue="keywords" @search="search" @close="close" :placeholder="searchplaceholder" width="604"/>
      <pem-tabs v-if="!(auth && ['cy', 'tz'].indexOf(auth.type) > -1)" :tabtype="tabtype" :list="tabs" @switchTab="switchTab" height="84"/>
    </div>
    <pem-loading v-show="isLoading"></pem-loading>
    <pem-dialog ref='alert' :text="alertText" :confirmBtnText="alertBtnText" :type="alertType" @confirm="alertCallback"></pem-dialog>
    <pem-operation-panel @backtotop="backtotop" />
  </div>
</template>

<script type="text/ecmascript-6">
import PemOperationPanel from 'components/pem-operation-panel/pem-operation-panel'
import PemInvestorList from 'components/pem-investor-list/pem-investor-list'
import PemProjectList from 'components/pem-project-list/pem-project-list'
import PemScrollView from 'components/scroll-view/scroll-view'
import PemLoading from 'components/pem-loading/pem-loading'
import PemSearch from 'components/pem-search/pem-search'
import PemTabs from 'components/pem-tabs/pem-tabs'
import PemIcon from 'components/pem-icon/pem-icon'
import PemDialog from 'components/dialog/dialog'
import { MiniSiteApi, ImgPath } from 'api'
import { mapState, mapActions } from 'vuex'
import { SEARCH_LIST_UPDATE, SEARCH_LIST_CLEAR } from 'store/minisite/search-list'
import { SEARCH_APPEND, SEARCH_CLEAR } from 'store/minisite/search'
import { setTitle } from 'common/js/util'

export default {
  components: {
    PemOperationPanel,
    PemInvestorList,
    PemProjectList,
    PemScrollView,
    PemLoading,
    PemSearch,
    PemDialog,
    PemTabs,
    PemIcon
  },
  data () {
    return {
      searchplaceholder: '可输入项目或投资人关键词',
      exactFlag: false,    // 是否精确匹配
      exactList: [],
      keywords: '',
      currentPage: 1,
      pageSize: 10,
      listenList: [],
      projectList: [],
      investorList: [],
      hotList: [],
      tabtype: 'project',
      tabs: [
        {text: '融资项目', type: 'project'},
        {text: '投资人', type: 'investor'}
      ],
      isInit: true,
      alertText: '',
      alertType: '',
      alertBtnText: '',
      alertBtnCallback: null,
      isLoading: false,
      isMore: true,
      isCy: false
    }
  },
  created () {
    this._initWXShare()
    this._setTitle('项目工场-搜索')
    setTimeout(() => {
      this._initData()
    }, 20)
  },
  computed: mapState({
    auth: state => state.auth,
    searchList: state => state.searchList
  }),
  beforeDestroy () {
    if (this.searchList) {
      let _searchList = JSON.parse(JSON.stringify(this.searchList))
      _searchList.keywords = this.keywords
      _searchList.y = this.$refs.scroll.scroll.y
      this.SEARCH_LIST_UPDATE(_searchList)
    }
  },
  methods: {
    _initWXShare () {
      this.wxShare({
        title: '为您推荐项目工场',
        content: '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！',
        linkUrl: window.location.origin
      })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _initData () {
      this.keywords = (this.searchList && this.searchList.keywords) ? this.searchList.keywords : (this.$route.query.k || '')
      this.tabtype = (this.searchList && this.searchList.tabtype) ? this.searchList.tabtype : (this.$route.query.tabtype || 'project')
      if (this.auth && this.auth.type === 'cy') this.tabtype = 'investor'
      if (this.auth && this.auth.type === 'tz') this.searchplaceholder = '可输入项目关键词'
      else if (this.auth && this.auth.type === 'cy') this.searchplaceholder = '可输入投资人关键词'
      else this.searchplaceholder = '可输入项目或投资人关键词'
      if (this.searchList && this.searchList.hotList && this.searchList.hotList.length > 0) {
        this.hotList = this.searchList.hotList
      } else {
        this.getHotwords()
      }
      this.isMore = true
      if (this.tabtype === 'project') {
        this.getProjectList('init')
      } else if (this.tabtype === 'investor') {
        this.getInvestorList('init')
      }
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    listMore () {
      if (!this.isLoading && this.isMore) {
        if (this.tabtype === 'project') {
          this.getProjectList('more')
        } else if (this.tabtype === 'investor') {
          this.getInvestorList('more')
        }
      }
    },
    listRefresh () {
      if (!this.isLoading) {
        this.isMore = true
        this.listClear()
        if (this.tabtype === 'project') {
          this.getProjectList('init')
        } else if (this.tabtype === 'investor') {
          this.getInvestorList('init')
        }
      }
    },
    listClear () {
      let _searchList = JSON.parse(JSON.stringify(this.searchList))
      _searchList.tabtype = null
      _searchList.keywords = null
      _searchList.list = []
      _searchList.y = 0
      this.SEARCH_LIST_UPDATE(_searchList)
    },
    search (value) {
      if (!value) return
      this.keywords = value.trim()
      this.isMore = true
      this.listClear()
      this.$refs.scroll.scrollTo(0, 0)
      if (this.tabtype === 'project') {
        this.getProjectList('init')
      } else if (this.tabtype === 'investor') {
        this.getInvestorList('init')
      }
    },
    close () {
      this.keywords = ''
      this.$router.goBack()
    },
    switchTab (type) {
      this.tabtype = type
      this.exactList = []
      if (!this.keywords) return
      this.isMore = true
      switch (type) {
        case 'project':
          this.investorList = []
          this.getProjectList('init')
          break
        case 'investor':
          this.projectList = []
          this.getInvestorList('init')
          break
        default:
          break
      }
    },
    alert (text, options = {}) {
      this.alertText = text
      this.alertType = options.type || 'alert'
      this.alertBtnText = options.btn || '确定'
      this.alertBtnCallback = options.callback || null
      this.$refs.alert.show()
    },
    alertCallback () {
      this.alertBtnCallback && this.alertBtnCallback()
    },
    getHotwords () {
      this._getHotwords(data => {
        if (+data.resultcode === 1) {
          this.hotList = (data.resultList || []).map(item => {
            return item.words
          })
        }
      })
    },
    getProjectList (type = 'init') {
      if (!this.keywords) return false
      this.isLoading = true
      if (this.hotList.indexOf(this.keywords) === -1) {
        this.SEARCH_APPEND(this.keywords)
      }
      if (type === 'init') {
        this.currentPage = 1
      }
      if (this.searchList &&
        this.searchList.list &&
        this.searchList.list.length > 0 &&
        this.searchList.keywords === this.keywords &&
        this.searchList.tabtype === this.tabtype &&
        this.searchList.tabtype === 'project' &&
        this.currentPage <= this.searchList.currentPage) {
        this.isLoading = false
        this.currentPage = +this.searchList.currentPage + 1
        if (+this.searchList.totalPage <= +this.searchList.currentPage) {
          this.isMore = false
        }
        this.projectList = this.searchList.list
        this.exactFlag = this.searchList.exactFlag
        this.exactList = this.searchList.exactList
        this.listenList = this.projectList
        if (type === 'init') {
          setTimeout(() => {
            this.$refs.scroll.scrollTo(0, this.searchList.y || 0)
          }, 21)
        }
        return false
      }
      this._getProjectList({
        currentPage: +this.currentPage,
        pageSize: +this.pageSize,
        keywords: this.keywords,
        tabtype: '100'
      }, (data) => {
        this.isInit = false
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = (+data.currentPage || 1) + 1
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          let _list = (type === 'init') ? [] : this.projectList
          _list = _list.concat(data.resultList || []).map(item => {
            item.logo = ImgPath + (item.logo || '').replace(ImgPath, '')
            if (item.iconList) {
              item.iconList = item.iconList.map(icon => {
                let _imgurl = icon.imgurl
                icon.imgurl = _imgurl ? (ImgPath + _imgurl.replace(ImgPath, '')) : null
                return icon
              })
            }
            return item
          })
          if (type === 'init') {
            this.exactFlag = data.exactFlag
            if (data.exactFlag) this.exactList = _list.splice(0, 1)
          }
          let _searchList = JSON.parse(JSON.stringify(this.searchList))
          _searchList.list = _list
          _searchList.keywords = this.keywords
          _searchList.tabtype = this.tabtype
          _searchList.exactFlag = this.exactFlag
          _searchList.exactList = this.exactList
          _searchList.currentPage = +data.currentPage
          _searchList.totalPage = +data.totalPage
          _searchList.y = this.$refs.scroll.scroll.y
          this.SEARCH_LIST_UPDATE(_searchList)
          this.projectList = _list
          this.listenList = this.projectList
        }
      })
    },
    getInvestorList (type = 'init') {
      if (!this.keywords) return false
      this.isLoading = true
      if (this.hotList.indexOf(this.keywords) === -1) {
        this.SEARCH_APPEND(this.keywords)
      }
      if (type === 'init') {
        this.currentPage = 1
      }
      if (this.searchList &&
        this.searchList.list &&
        this.searchList.list.length > 0 &&
        this.searchList.keywords === this.keywords &&
        this.searchList.tabtype === this.tabtype &&
        this.searchList.tabtype === 'investor' &&
        this.currentPage <= this.searchList.currentPage) {
        this.isLoading = false
        this.currentPage = +this.searchList.currentPage + 1
        if (+this.searchList.totalPage <= +this.searchList.currentPage) {
          this.isMore = false
        }
        this.investorList = this.searchList.list
        this.exactFlag = this.searchList.exactFlag
        this.exactList = this.searchList.exactList
        this.listenList = this.investorList
        setTimeout(() => {
          this.$refs.scroll.scrollTo(0, this.searchList.y || 0)
        }, 21)
        return false
      }
      this._getInvestorList({
        currentPage: +this.currentPage,
        pageSize: +this.pageSize,
        keywords: this.keywords,
        tabtype: '100'
      }, (data) => {
        this.isInit = false
        this.isLoading = false
        if (+data.resultcode === 1) {
          this.currentPage = +data.currentPage || 1
          this.totalPage = +data.totalPage
          if (+data.totalPage <= +data.currentPage) {
            this.isMore = false
          }
          this.exactFlag = data.exactFlag
          let _list = (type === 'init') ? [] : this.investorList
          _list = _list.concat(data.resultList || []).map(item => {
            if (item.logo && item.logo.indexOf('user_xcx/default_investor_img') === -1) {
              item['logo'] = ImgPath + item.logo.replace(ImgPath, '')
            } else {
              let userZj = (item.zj || '1').toUpperCase()
              let bgNum = userZj.charCodeAt(userZj.length - 1) % 20
              item['logo'] = `https://xmgc.pemarket.com.cn/user_xcx/default_investor_img${bgNum}.png`
            }
            return item
          })
          if (type === 'init') {
            this.exactFlag = data.exactFlag
            if (data.exactFlag) this.exactList = _list.splice(0, 1)
          }
          let _searchList = JSON.parse(JSON.stringify(this.searchList))
          _searchList.list = _list
          _searchList.keywords = this.keywords
          _searchList.tabtype = this.tabtype
          _searchList.exactFlag = this.exactFlag
          _searchList.exactList = this.exactList
          _searchList.currentPage = +data.currentPage
          _searchList.totalPage = +data.totalPage
          _searchList.y = this.$refs.scroll.scroll.y
          this.SEARCH_LIST_UPDATE(_searchList)
          this.investorList = _list
          this.listenList = this.investorList
        }
      })
    },
    _getProjectList (options, callback) {
      MiniSiteApi.projectList(options).then(data => {
        callback(data)
      })
    },
    _getInvestorList (options, callback) {
      MiniSiteApi.getInvestorList(options).then(data => {
        callback(data)
      })
    },
    _getHotwords (callback) {
      MiniSiteApi.getHotwords().then(data => {
        callback(data)
      })
    },
    ...mapActions([
      SEARCH_APPEND,
      SEARCH_CLEAR,
      SEARCH_LIST_UPDATE,
      SEARCH_LIST_CLEAR
    ])
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .search
    font-size: 0.24rem
    .page-panel
      top: 1.1rem
      &.page-panel-yk
        top: 1.81rem
    .search-hot-history-wrapper
      line-top()
      .search-hot-history
        color: $color-text
        padding: 0.1rem 0.3rem
        &.search-hot
            line-bottom()
        .search-hot-title
          padding: 0.3rem 0
          font-size: 0
          line-height: 0.32rem
          overflow: hidden
          span, i
            font-size: 0.24rem
            vertical-align: middle
          span
            margin-left: 0.1rem
            &.history-delete-all-btn
              padding-left: 0.4rem
              height: 0.35rem
        .search-hot-history-content
          font-size: 0
          .search-hot-history-item
            display: inline-block
            font-size: 0.26rem
            line-height: 0.36rem
            padding: 0.1rem 0.3rem
            margin-right: 0.2rem
            margin-bottom: 0.3rem
            border-radius: 0.56rem
            border: 0.02rem solid #C9C9C9
            &.search-history-item
              position: relative
              padding-right: 0.6rem
              max-width: 5.8rem
              no-wrap()
              .history-delete-btn
                position: absolute
                right: 0
                top: 0
                width: 0.6rem
                height: 0.56rem
                z-index: 1
                i
                  position: absolute
                  left: 0.1rem
                  top: 0.18rem
        .search-hot-history-empty
          padding: 0.4rem
          font-size: 0.24rem
          color: #A7A5A3
          text-align: center
    .exact-matching
      line-top()
      &.borderbottom
        border-bottom: 0.1rem solid #f6f6f6
    .no-matching
      padding: 0.2rem 0.3rem
      font-size: 0
      line-top()
      line-bottom()
      span, i
        display: inline-block
        vertical-align: middle
      span
        font-size: 0.24rem
        color: #202020
        margin-left: 0.22rem
    .empty-result
      padding-top: 0.08rem
      text-align: center
      font-size: 0.32rem
      color: #202020
  .search-list-header
    position: fixed
    top: 0
    left: 0
    z-index: 100
    width: 100%
</style>



